'use client';

import React, { useState, useEffect, useRef } from 'react';
import { Calendar, CheckSquare, BrainCircuit, Database, LayoutGrid, Search, Palette, X, Moon, Sun, Check, ArrowLeft, Plus, StickyNote, Type } from 'lucide-react';
import { DndContext, closestCenter, KeyboardSensor, PointerSensor, useSensor, useSensors, DragOverlay } from '@dnd-kit/core';
import { arrayMove, SortableContext, sortableKeyboardCoordinates, useSortable, rectSortingStrategy } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';

import BentoCard from '../components/BentoCard';
import Agenda from '../components/Agenda';
import Tasks from '../components/Tasks';
import AIChat from '../components/AIChat';
import Notes from '../components/Notes';
import Vault from '../components/Vault';
import SystemMonitor from '../components/SystemMonitor';

// Helper to convert hex to rgba with opacity
const hexToRgba = (hex, alpha) => {
  // Simple hex parsing
  let c;
  if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)){
      c= hex.substring(1).split('');
      if(c.length== 3){
          c= [c[0], c[0], c[1], c[1], c[2], c[2]];
      }
      c= '0x'+c.join('');
      return 'rgba('+[(c>>16)&255, (c>>8)&255, c&255].join(',')+','+alpha+')';
  }
  return `rgba(115, 42, 223, ${alpha})`; // Fallback
};

// Theme Definitions
const THEMES = [
  { id: 'nexus', name: 'Nexus', primary: '#732ADF', secondary: '#65BBBD' },
  { id: 'cyber', name: 'Cyber', primary: '#FF0055', secondary: '#00E5FF' },
  { id: 'emerald', name: 'Forest', primary: '#10B981', secondary: '#F59E0B' },
  { id: 'monochrome', name: 'Mono', primary: '#6B7280', secondary: '#E5E7EB' },
  { id: 'sunset', name: 'Sunset', primary: '#F97316', secondary: '#8B5CF6' },
];

// Wrapper for Sortable Items
const SortableItem = ({ id, children, className }) => {
    const {
      attributes,
      listeners,
      setNodeRef,
      transform,
      transition,
      isDragging
    } = useSortable({ id });
  
    // Improve transition for "Apple-like" smoothness
    const style = {
      transform: CSS.Transform.toString(transform),
      transition: transition || 'transform 250ms cubic-bezier(0.25, 1, 0.5, 1)',
      zIndex: isDragging ? 999 : 'auto',
      position: 'relative',
      opacity: isDragging ? 0.8 : 1,
      scale: isDragging ? 1.05 : 1,
    };
  
    return (
      <div ref={setNodeRef} style={style} className={className} {...attributes}>
         {/* Pass listeners to children so they can attach to drag handle */}
         {React.Children.map(children, child => {
             if (React.isValidElement(child)) {
                 return React.cloneElement(child, { dragHandleProps: listeners });
             }
             return child;
         })}
      </div>
    );
  };

export default function Home() {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [searchQuery, setSearchQuery] = useState('');
  const searchInputRef = useRef(null);
  
  // Customization & Theme State
  const [isCustomizing, setIsCustomizing] = useState(false);
  const [showAddWidgetModal, setShowAddWidgetModal] = useState(false);
  
  // Initialize Theme Mode from Local Storage
  const [themeMode, setThemeMode] = useState('dark');
  const [activePaletteId, setActivePaletteId] = useState('nexus');
  const [activePalette, setActivePalette] = useState(THEMES[0]);

  // Widget Layout State
  const [staticLayout, setStaticLayout] = useState({
      monitor: { col: 2, row: 1 },
      tasks: { col: 2, row: 2 },
      ai: { col: 2, row: 2 },
      agenda: { col: 2, row: 2 },
      vault: { col: 2, row: 1 },
      notes: { col: 2, row: 1 }
  });

  const [widgetOrder, setWidgetOrder] = useState(['monitor', 'tasks', 'ai', 'agenda', 'vault', 'notes']);
  const [customWidgets, setCustomWidgets] = useState([]);

  // New Widget Form State
  const [newWidgetTitle, setNewWidgetTitle] = useState('');
  const [newWidgetCols, setNewWidgetCols] = useState(1);
  const [newWidgetRows, setNewWidgetRows] = useState(1);
  const [newWidgetColor, setNewWidgetColor] = useState('#732ADF');

  // Colors for static widgets (customizable)
  const [colors, setColors] = useState({
      monitor: '#732ADF',
      tasks: '#10B981',
      ai: '#F59E0B',
      agenda: '#3B82F6',
      vault: '#EC4899',
      notes: '#8B5CF6'
  });

  // Load state from localStorage on mount
  useEffect(() => {
    if (typeof window !== 'undefined') {
        const savedThemeMode = localStorage.getItem('nexus_theme_mode');
        if (savedThemeMode) setThemeMode(savedThemeMode);

        const savedPaletteId = localStorage.getItem('nexus_palette_id');
        if (savedPaletteId) {
            setActivePaletteId(savedPaletteId);
            const palette = THEMES.find(t => t.id === savedPaletteId);
            if (palette) setActivePalette(palette);
        }

        const savedLayout = localStorage.getItem('nexus_static_layout');
        if (savedLayout) setStaticLayout(JSON.parse(savedLayout));

        const savedOrder = localStorage.getItem('nexus_widget_order');
        if (savedOrder) setWidgetOrder(JSON.parse(savedOrder));

        const savedCustomWidgets = localStorage.getItem('nexus_custom_widgets');
        if (savedCustomWidgets) setCustomWidgets(JSON.parse(savedCustomWidgets));

        const savedColors = localStorage.getItem('nexus_widget_colors');
        if (savedColors) setColors(JSON.parse(savedColors));
    }
  }, []);

  // Persist state changes
  useEffect(() => {
      if (typeof window !== 'undefined') {
          localStorage.setItem('nexus_theme_mode', themeMode);
          localStorage.setItem('nexus_palette_id', activePaletteId);
          localStorage.setItem('nexus_static_layout', JSON.stringify(staticLayout));
          localStorage.setItem('nexus_widget_order', JSON.stringify(widgetOrder));
          localStorage.setItem('nexus_custom_widgets', JSON.stringify(customWidgets));
          localStorage.setItem('nexus_widget_colors', JSON.stringify(colors));
      }
  }, [themeMode, activePaletteId, staticLayout, widgetOrder, customWidgets, colors]);

  // Apply Theme Mode Class to Body
  useEffect(() => {
      if (themeMode === 'dark') {
          document.documentElement.classList.add('dark');
          document.documentElement.removeAttribute('data-theme');
      } else {
          document.documentElement.classList.remove('dark');
          document.documentElement.setAttribute('data-theme', 'light');
      }
  }, [themeMode]);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = (event) => {
    const { active, over } = event;
    
    if (active.id !== over.id) {
      setWidgetOrder((items) => {
        const oldIndex = items.indexOf(active.id);
        const newIndex = items.indexOf(over.id);
        return arrayMove(items, oldIndex, newIndex);
      });
    }
  };

  const toggleThemeMode = () => {
      setThemeMode(prev => prev === 'light' ? 'dark' : 'light');
  };

  const changePalette = (theme) => {
      setActivePaletteId(theme.id);
      setActivePalette(theme);
  };

  const updateColor = (key, color) => {
      setColors(prev => ({ ...prev, [key]: color }));
  };

  const addCustomWidget = () => {
      if (!newWidgetTitle) return;
      const widget = {
          id: `custom-${Date.now()}`,
          title: newWidgetTitle,
          content: '',
          colSpan: newWidgetCols,
          rowSpan: newWidgetRows,
          color: newWidgetColor
      };
      setCustomWidgets(prev => [...prev, widget]);
      setShowAddWidgetModal(false);
      setNewWidgetTitle('');
      setNewWidgetCols(1);
      setNewWidgetRows(1);
  };

  const removeCustomWidget = (id) => {
      setCustomWidgets(prev => prev.filter(w => w.id !== id));
  };

  const updateWidgetContent = (id, content) => {
      setCustomWidgets(prev => prev.map(w => w.id === id ? { ...w, content } : w));
  };

  const updateWidgetColor = (id, color) => {
      setCustomWidgets(prev => prev.map(w => w.id === id ? { ...w, color } : w));
  };

  const updateWidgetSize = (id, cols, rows) => {
      setCustomWidgets(prev => prev.map(w => w.id === id ? { ...w, colSpan: cols, rowSpan: rows } : w));
  };
  
  const updateStaticWidgetSize = (key, cols, rows) => {
      setStaticLayout(prev => ({
          ...prev,
          [key]: { col: cols, row: rows }
      }));
  };

  const getGridClasses = (col, row) => {
      return `md:col-span-${col} md:row-span-${row}`;
  };

  const renderWidget = (id) => {
      // 1. Static Widgets
      if (id === 'monitor') {
          return (
            <BentoCard 
                key={id}
                colSpanInt={staticLayout.monitor.col}
                rowSpanInt={staticLayout.monitor.row}
                onResize={(c, r) => updateStaticWidgetSize('monitor', c, r)}
                className="h-full min-h-[140px]"
                glowColor={hexToRgba(colors.monitor, 0.15)}
                isCustomizing={isCustomizing}
                pickerColor={colors.monitor}
                onColorChange={(c) => updateColor('monitor', c)}
            >
                <SystemMonitor />
            </BentoCard>
          );
      }
      if (id === 'tasks') {
          return (
            <BentoCard 
                key={id}
                colSpanInt={staticLayout.tasks.col}
                rowSpanInt={staticLayout.tasks.row}
                onResize={(c, r) => updateStaticWidgetSize('tasks', c, r)}
                title="Missions" 
                icon={<CheckSquare />}
                glowColor={hexToRgba(colors.tasks, 0.2)}
                className="h-full min-h-[140px]"
                isCustomizing={isCustomizing}
                pickerColor={colors.tasks}
                onColorChange={(c) => updateColor('tasks', c)}
                onTripleClick={() => setActiveTab('tasks')}
            >
                <Tasks searchQuery={searchQuery} />
            </BentoCard>
          );
      }
      if (id === 'ai') {
          return (
            <BentoCard 
                key={id}
                colSpanInt={staticLayout.ai.col}
                rowSpanInt={staticLayout.ai.row}
                onResize={(c, r) => updateStaticWidgetSize('ai', c, r)}
                title="Nexus AI Core" 
                icon={<BrainCircuit />}
                glowColor={hexToRgba(colors.ai, 0.4)}
                className="h-full min-h-[140px]"
                isCustomizing={isCustomizing}
                pickerColor={colors.ai}
                onColorChange={(c) => updateColor('ai', c)}
                onTripleClick={() => setActiveTab('ai')}
            >
                <AIChat />
            </BentoCard>
          );
      }
      if (id === 'agenda') {
          return (
            <BentoCard 
                key={id}
                colSpanInt={staticLayout.agenda.col}
                rowSpanInt={staticLayout.agenda.row}
                onResize={(c, r) => updateStaticWidgetSize('agenda', c, r)}
                title="Timeline" 
                icon={<Calendar />}
                glowColor={hexToRgba(colors.agenda, 0.3)}
                className="h-full min-h-[140px]"
                isCustomizing={isCustomizing}
                pickerColor={colors.agenda}
                onColorChange={(c) => updateColor('agenda', c)}
                onTripleClick={() => setActiveTab('calendar')}
            >
                <Agenda searchQuery={searchQuery} />
            </BentoCard>
          );
      }
      if (id === 'vault') {
          return (
            <BentoCard 
                key={id}
                colSpanInt={staticLayout.vault.col}
                rowSpanInt={staticLayout.vault.row}
                onResize={(c, r) => updateStaticWidgetSize('vault', c, r)}
                title="Data Vault" 
                icon={<Database />}
                className="h-full min-h-[140px]"
                glowColor={hexToRgba(colors.vault, 0.5)}
                isCustomizing={isCustomizing}
                pickerColor={colors.vault}
                onColorChange={(c) => updateColor('vault', c)}
                onTripleClick={() => setActiveTab('vault')}
            >
                <Vault searchQuery={searchQuery} />
            </BentoCard>
          );
      }
      if (id === 'notes') {
          return (
            <BentoCard 
                key={id}
                colSpanInt={staticLayout.notes.col}
                rowSpanInt={staticLayout.notes.row}
                onResize={(c, r) => updateStaticWidgetSize('notes', c, r)}
                title="Neural Notes" 
                icon={<LayoutGrid />}
                glowColor={hexToRgba(colors.notes, 0.1)}
                className="h-full min-h-[140px]"
                isCustomizing={isCustomizing}
                pickerColor={colors.notes}
                onColorChange={(c) => updateColor('notes', c)}
                onTripleClick={() => setActiveTab('notes')}
            >
                <div className="h-full w-full">
                    <Notes searchQuery={searchQuery} />
                </div>
            </BentoCard>
          );
      }

      // 2. Custom Widgets
      const customWidget = customWidgets.find(w => w.id === id);
      if (customWidget) {
          return (
            <BentoCard 
                key={id}
                colSpanInt={customWidget.colSpan}
                rowSpanInt={customWidget.rowSpan}
                onResize={(c, r) => updateWidgetSize(customWidget.id, c, r)}
                title={customWidget.title}
                icon={<StickyNote />}
                glowColor={hexToRgba(customWidget.color, 0.3)}
                className="h-full"
                isCustomizing={isCustomizing}
                pickerColor={customWidget.color}
                onColorChange={(c) => updateWidgetColor(customWidget.id, c)}
                onDelete={() => removeCustomWidget(customWidget.id)}
            >
                <textarea 
                    className="w-full h-full bg-transparent resize-none focus:outline-none text-sm text-gray-300 placeholder:text-gray-600 font-mono"
                    placeholder="Enter widget content..."
                    value={customWidget.content}
                    onChange={(e) => updateWidgetContent(customWidget.id, e.target.value)}
                />
            </BentoCard>
          );
      }
      return null;
  };

  const getWidgetGridClass = (id) => {
      if (id === 'monitor') return getGridClasses(staticLayout.monitor.col, staticLayout.monitor.row);
      if (id === 'tasks') return getGridClasses(staticLayout.tasks.col, staticLayout.tasks.row);
      if (id === 'ai') return getGridClasses(staticLayout.ai.col, staticLayout.ai.row);
      if (id === 'agenda') return getGridClasses(staticLayout.agenda.col, staticLayout.agenda.row);
      if (id === 'vault') return getGridClasses(staticLayout.vault.col, staticLayout.vault.row);
      if (id === 'notes') return getGridClasses(staticLayout.notes.col, staticLayout.notes.row);
      const custom = customWidgets.find(w => w.id === id);
      if (custom) return getGridClasses(custom.colSpan, custom.rowSpan);
      return 'md:col-span-1 md:row-span-1';
  };

  const renderContent = () => {
    // --- Full Page Views ---
    if (activeTab === 'calendar') {
      return (
        <div className="flex-1 w-full pb-8 animate-fade-in min-h-[800px]">
          <BentoCard className="h-full" title="Master Timeline" icon={<Calendar />} glowColor={hexToRgba(activePalette.secondary, 0.3)}>
             <div className="h-full p-4">
                <Agenda searchQuery={searchQuery} />
             </div>
          </BentoCard>
        </div>
      );
    }
    if (activeTab === 'tasks') {
        return (
          <div className="flex-1 w-full pb-8 animate-fade-in min-h-[800px]">
            <BentoCard className="h-full" title="Command Center" icon={<CheckSquare />} glowColor={hexToRgba(activePalette.primary, 0.2)}>
               <div className="h-full p-4 max-w-3xl mx-auto">
                  <Tasks searchQuery={searchQuery} />
               </div>
            </BentoCard>
          </div>
        );
      }
      if (activeTab === 'notes') {
        return (
          <div className="flex-1 w-full pb-8 animate-fade-in min-h-[800px]">
            <BentoCard className="h-full" title="Neural Notes Workspace" icon={<LayoutGrid />} glowColor={hexToRgba(colors.notes, 0.2)}>
               <div className="h-full p-4">
                  <Notes searchQuery={searchQuery} />
               </div>
            </BentoCard>
          </div>
        );
      }
      if (activeTab === 'vault') {
        return (
          <div className="flex-1 w-full pb-8 animate-fade-in min-h-[800px]">
            <BentoCard className="h-full" title="Data Vault Archive" icon={<Database />} glowColor={hexToRgba(colors.vault, 0.3)}>
               <div className="h-full p-4 max-w-4xl mx-auto">
                  <Vault searchQuery={searchQuery} />
               </div>
            </BentoCard>
          </div>
        );
      }
      if (activeTab === 'ai') {
          return (
            <div className="flex-1 w-full pb-8 animate-fade-in min-h-[800px]">
              <BentoCard className="h-full" title="Nexus AI Core - Full Interface" icon={<BrainCircuit />} glowColor={hexToRgba(colors.ai, 0.4)}>
                 <div className="h-full p-4 max-w-4xl mx-auto">
                    <AIChat />
                 </div>
              </BentoCard>
            </div>
          );
        }

    // --- Dashboard View (Draggable) ---
    return (
        <DndContext 
            sensors={sensors} 
            collisionDetection={closestCenter} 
            onDragEnd={handleDragEnd}
        >
            <SortableContext items={widgetOrder} strategy={rectSortingStrategy}>
                <div className="grid grid-cols-1 md:grid-cols-4 gap-6 pb-12 animate-fade-in auto-rows-[180px]">
                    {widgetOrder.map(id => (
                         <SortableItem key={id} id={id} className={getWidgetGridClass(id)}>
                             {renderWidget(id)}
                         </SortableItem>
                    ))}
                </div>
            </SortableContext>
        </DndContext>
    );
  };

  const isSubPage = activeTab !== 'dashboard';

  return (
    <div className="min-h-screen bg-nexus-deep text-nexus-text p-4 md:p-8 font-sans selection:bg-nexus-purple selection:text-white relative overflow-x-hidden transition-colors duration-300">
      
      {/* Background Ambience */}
      <div className="fixed top-0 left-0 w-full h-full overflow-hidden pointer-events-none z-0">
        <div 
          className="absolute top-[-10%] left-[-10%] w-[40%] h-[40%] bg-nexus-purple/20 rounded-full blur-[120px] animate-pulse-slow" 
          style={{ backgroundColor: isCustomizing ? colors.ai + '40' : undefined }} 
        />
        <div 
          className="absolute bottom-[-10%] right-[-10%] w-[40%] h-[40%] bg-nexus-teal/10 rounded-full blur-[120px] animate-pulse-slow" 
          style={{ animationDelay: '2s', backgroundColor: isCustomizing ? colors.monitor + '20' : undefined }}
        />
      </div>

      <div className="relative z-10 max-w-7xl mx-auto flex flex-col min-h-screen">
        
        {/* Header */}
        <header className="flex flex-col md:flex-row justify-between items-center mb-8 gap-4">
          <div className="flex items-center gap-4 w-full md:w-auto">
             {isSubPage && (
                 <button 
                    onClick={() => setActiveTab('dashboard')}
                    className="p-2 rounded-full bg-white/5 hover:bg-white/10 transition-colors border border-white/10"
                 >
                     <ArrowLeft size={20} />
                 </button>
             )}
             <div>
                <h1 className="text-4xl font-bold tracking-tighter bg-clip-text text-transparent bg-gradient-to-r from-white to-gray-400">
                  NEXUS OMNIA
                </h1>
                <p className="text-sm text-gray-400 tracking-widest uppercase opacity-70">
                  Personal Command Interface
                </p>
             </div>
          </div>

          <div className="flex items-center gap-3 w-full md:w-auto justify-end">
             <div className="relative group flex-1 md:flex-none">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500" size={16} />
                <input 
                  ref={searchInputRef}
                  type="text" 
                  placeholder="Search system..." 
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full md:w-64 bg-black/20 border border-white/10 rounded-full py-2 pl-10 pr-4 text-sm focus:outline-none focus:border-nexus-purple/50 transition-all"
                />
             </div>
             
             <button 
                onClick={toggleThemeMode}
                className="p-2 rounded-full bg-white/5 hover:bg-white/10 transition-colors border border-white/10"
                title="Toggle Theme"
             >
                {themeMode === 'dark' ? <Sun size={18} /> : <Moon size={18} />}
             </button>

             <button 
                onClick={() => setIsCustomizing(!isCustomizing)}
                className={`p-2 rounded-full transition-colors border border-white/10 ${isCustomizing ? 'bg-nexus-purple text-white' : 'bg-white/5 hover:bg-white/10'}`}
                title="Customize Layout"
             >
                <Palette size={18} />
             </button>
          </div>
        </header>

        {/* Customization Toolbar */}
        {isCustomizing && (
            <div className="mb-8 p-4 rounded-2xl bg-black/30 border border-white/10 backdrop-blur-md animate-slide-down">
                <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-6">
                    <div>
                        <h3 className="text-sm font-bold text-gray-300 mb-3 uppercase tracking-wider">Color Theme</h3>
                        <div className="flex gap-3">
                            {THEMES.map(theme => (
                                <button
                                    key={theme.id}
                                    onClick={() => changePalette(theme)}
                                    className={`w-8 h-8 rounded-full border-2 transition-transform hover:scale-110 ${activePaletteId === theme.id ? 'border-white scale-110' : 'border-transparent'}`}
                                    style={{ background: `linear-gradient(135deg, ${theme.primary}, ${theme.secondary})` }}
                                    title={theme.name}
                                />
                            ))}
                        </div>
                    </div>

                    <div>
                        <h3 className="text-sm font-bold text-gray-300 mb-3 uppercase tracking-wider">Widgets</h3>
                        <div className="flex gap-3">
                            <button 
                                onClick={() => setShowAddWidgetModal(true)}
                                className="flex items-center gap-2 px-4 py-2 rounded-lg bg-white/5 hover:bg-white/10 border border-white/10 transition-colors text-sm"
                            >
                                <Plus size={16} />
                                Add Custom Widget
                            </button>
                            <button 
                                onClick={() => {
                                    setStaticLayout({
                                        monitor: { col: 2, row: 1 },
                                        tasks: { col: 2, row: 2 },
                                        ai: { col: 2, row: 2 },
                                        agenda: { col: 2, row: 2 },
                                        vault: { col: 2, row: 1 },
                                        notes: { col: 2, row: 1 }
                                    });
                                    setWidgetOrder(['monitor', 'tasks', 'ai', 'agenda', 'vault', 'notes']);
                                }}
                                className="px-4 py-2 rounded-lg bg-red-500/10 hover:bg-red-500/20 border border-red-500/20 text-red-400 transition-colors text-sm"
                            >
                                Reset Layout
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )}

        {/* Main Content Area */}
        <main className="flex-1 relative">
            {renderContent()}
        </main>

        {/* Footer */}
        <footer className="mt-auto py-6 text-center text-xs text-gray-600 border-t border-white/5">
            <p>NEXUS OMNIA SYSTEM v2.4.0 // AUTHORIZED ACCESS ONLY</p>
        </footer>

      </div>

      {/* Add Widget Modal */}
      {showAddWidgetModal && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-sm p-4">
              <div className="bg-[#111] border border-white/10 rounded-2xl p-6 w-full max-w-md shadow-2xl animate-scale-in">
                  <div className="flex justify-between items-center mb-6">
                      <h2 className="text-xl font-bold">Add Custom Widget</h2>
                      <button onClick={() => setShowAddWidgetModal(false)} className="text-gray-400 hover:text-white">
                          <X size={20} />
                      </button>
                  </div>

                  <div className="space-y-4">
                      <div>
                          <label className="block text-xs text-gray-400 mb-1 uppercase">Title</label>
                          <input 
                              type="text" 
                              value={newWidgetTitle}
                              onChange={(e) => setNewWidgetTitle(e.target.value)}
                              className="w-full bg-white/5 border border-white/10 rounded-lg p-2 text-sm focus:border-nexus-purple/50 outline-none"
                              placeholder="Widget Title"
                          />
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                          <div>
                              <label className="block text-xs text-gray-400 mb-1 uppercase">Columns (Width)</label>
                              <select 
                                  value={newWidgetCols}
                                  onChange={(e) => setNewWidgetCols(Number(e.target.value))}
                                  className="w-full bg-white/5 border border-white/10 rounded-lg p-2 text-sm outline-none"
                              >
                                  {[1, 2, 3, 4].map(n => <option key={n} value={n}>{n}</option>)}
                              </select>
                          </div>
                          <div>
                              <label className="block text-xs text-gray-400 mb-1 uppercase">Rows (Height)</label>
                              <select 
                                  value={newWidgetRows}
                                  onChange={(e) => setNewWidgetRows(Number(e.target.value))}
                                  className="w-full bg-white/5 border border-white/10 rounded-lg p-2 text-sm outline-none"
                              >
                                  {[1, 2, 3, 4].map(n => <option key={n} value={n}>{n}</option>)}
                              </select>
                          </div>
                      </div>

                      <div>
                          <label className="block text-xs text-gray-400 mb-1 uppercase">Accent Color</label>
                          <div className="flex gap-2 flex-wrap">
                              {['#732ADF', '#10B981', '#F59E0B', '#3B82F6', '#EC4899', '#EF4444'].map(c => (
                                  <button
                                      key={c}
                                      onClick={() => setNewWidgetColor(c)}
                                      className={`w-8 h-8 rounded-full border-2 ${newWidgetColor === c ? 'border-white' : 'border-transparent'}`}
                                      style={{ backgroundColor: c }}
                                  />
                              ))}
                          </div>
                      </div>

                      <button 
                          onClick={addCustomWidget}
                          disabled={!newWidgetTitle}
                          className="w-full py-3 mt-4 bg-nexus-purple hover:bg-nexus-purple/80 text-white rounded-lg font-medium transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                          Create Widget
                      </button>
                  </div>
              </div>
          </div>
      )}
    </div>
  );
}
