import React, { useState, useEffect } from 'react';
import { Folder, Link as LinkIcon, Youtube, FileText, Plus, X, Save, Trash2, Search } from 'lucide-react';

const Vault = ({ searchQuery = '' }) => {
  // Default items as fallback
  const defaultItems = [
    { id: '1', title: 'Calculus Syllabus', url: '#', category: 'doc' },
    { id: '2', title: 'React Crash Course', url: '#', category: 'youtube' },
    { id: '3', title: 'Project Drive', url: '#', category: 'drive' },
    { id: '4', title: 'Design System', url: '#', category: 'other' },
  ];

  // Initialize state from localStorage or defaults
  const [items, setItems] = useState(() => {
    if (typeof window !== 'undefined') {
        const saved = localStorage.getItem('nexus_vault_links');
        return saved ? JSON.parse(saved) : defaultItems;
    }
    return defaultItems;
  });

  const [isAdding, setIsAdding] = useState(false);
  const [newTitle, setNewTitle] = useState('');
  const [newUrl, setNewUrl] = useState('');
  const [newCategory, setNewCategory] = useState('other');
  const [localSearch, setLocalSearch] = useState('');

  // Persist to localStorage whenever items change
  useEffect(() => {
    localStorage.setItem('nexus_vault_links', JSON.stringify(items));
  }, [items]);

  const handleAdd = () => {
    if (!newTitle.trim() || !newUrl.trim()) return;

    const newItem = {
      id: Date.now().toString(),
      title: newTitle,
      url: newUrl.startsWith('http') ? newUrl : `https://${newUrl}`,
      category: newCategory
    };

    setItems([...items, newItem]);
    setIsAdding(false);
    resetForm();
  };

  const handleDelete = (id, e) => {
    e.preventDefault(); // Prevent link click
    e.stopPropagation();
    setItems(items.filter(item => item.id !== id));
  };

  const resetForm = () => {
    setNewTitle('');
    setNewUrl('');
    setNewCategory('other');
  };

  const getIcon = (cat) => {
    switch(cat) {
      case 'youtube': return <Youtube size={16} className="text-red-400" />;
      case 'doc': return <FileText size={16} className="text-blue-400" />;
      case 'drive': return <Folder size={16} className="text-yellow-400" />;
      default: return <LinkIcon size={16} className="text-nexus-teal" />;
    }
  };

  const filteredItems = items.filter(item => {
      const globalMatch = !searchQuery || 
             item.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
             item.url.toLowerCase().includes(searchQuery.toLowerCase()) ||
             item.category.toLowerCase().includes(searchQuery.toLowerCase());
      
      const localMatch = !localSearch || 
             item.title.toLowerCase().includes(localSearch.toLowerCase()) ||
             item.url.toLowerCase().includes(localSearch.toLowerCase()) ||
             item.category.toLowerCase().includes(localSearch.toLowerCase());

      return globalMatch && localMatch;
  });

  return (
    <div className="h-full relative overflow-hidden flex flex-col">
      {isAdding ? (
        <div className="h-full flex flex-col gap-3 animate-fade-in p-1">
          <div className="flex justify-between items-center mb-1">
            <h4 className="text-sm font-semibold text-white">Add Resource</h4>
            <button 
                onClick={() => setIsAdding(false)} 
                className="text-gray-400 hover:text-white p-1 hover:bg-white/10 rounded-full transition-colors"
            >
              <X size={14} />
            </button>
          </div>
          
          <div className="space-y-2 flex-1">
            <input 
                type="text" 
                placeholder="Resource Title" 
                value={newTitle}
                onChange={e => setNewTitle(e.target.value)}
                className="w-full bg-black/20 border border-white/10 rounded-lg px-3 py-2 text-xs text-white focus:border-nexus-teal focus:ring-1 focus:ring-nexus-teal/50 outline-none placeholder:text-gray-600 transition-all"
                autoFocus
            />
            
            <input 
                type="text" 
                placeholder="URL (e.g. google.com)" 
                value={newUrl}
                onChange={e => setNewUrl(e.target.value)}
                className="w-full bg-black/20 border border-white/10 rounded-lg px-3 py-2 text-xs text-white focus:border-nexus-teal focus:ring-1 focus:ring-nexus-teal/50 outline-none placeholder:text-gray-600 transition-all"
            />
            
            <div className="grid grid-cols-4 gap-2 mt-2">
                {['doc', 'youtube', 'drive', 'other'].map(cat => (
                <button
                    key={cat}
                    onClick={() => setNewCategory(cat)}
                    className={`
                        p-2 rounded-lg border flex flex-col items-center justify-center gap-1 transition-all h-16
                        ${newCategory === cat 
                            ? 'bg-nexus-teal/10 border-nexus-teal text-white shadow-[0_0_10px_rgba(101,187,189,0.2)]' 
                            : 'border-white/5 text-gray-500 hover:bg-white/5 hover:text-gray-300'}
                    `}
                    title={cat}
                >
                    {getIcon(cat)}
                    <span className="text-[9px] uppercase font-bold tracking-wider">{cat}</span>
                </button>
                ))}
            </div>
          </div>

          <button 
            onClick={handleAdd}
            className="w-full bg-nexus-teal text-nexus-deep font-bold py-2.5 rounded-xl text-xs hover:bg-white transition-all flex items-center justify-center gap-2 mt-2 shadow-lg hover:shadow-nexus-teal/50"
          >
            <Save size={14} /> Save to Vault
          </button>
        </div>
      ) : (
        <>
            {/* Search Bar */}
            <div className="relative mb-3 group shrink-0">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <Search size={12} className="text-gray-500 group-focus-within:text-nexus-teal transition-colors" />
                </div>
                <input 
                    type="text" 
                    value={localSearch}
                    onChange={(e) => setLocalSearch(e.target.value)}
                    placeholder="Filter vault..." 
                    className="w-full bg-black/20 border border-white/10 rounded-xl pl-9 pr-8 py-2 text-xs text-white focus:outline-none focus:border-nexus-teal/50 focus:bg-white/5 transition-all font-mono placeholder:text-gray-600"
                />
                {localSearch && (
                    <button 
                        onClick={() => setLocalSearch('')}
                        className="absolute inset-y-0 right-0 pr-2 flex items-center text-gray-500 hover:text-white"
                    >
                        <X size={12} />
                    </button>
                )}
            </div>

            <div className="flex-1 overflow-y-auto custom-scrollbar pr-1">
            <div className="grid grid-cols-2 gap-3">
                {filteredItems.map(item => (
                <a 
                    key={item.id} 
                    href={item.url}
                    target="_blank"
                    rel="noreferrer"
                    className="group relative flex flex-col p-3 rounded-xl bg-white/5 border border-white/5 hover:border-nexus-teal/50 transition-all hover:-translate-y-1 hover:shadow-lg hover:shadow-nexus-teal/10"
                >
                    <div className="flex items-center justify-between mb-2">
                    <div className="p-2 bg-black/30 rounded-lg group-hover:bg-nexus-teal/20 transition-colors">
                        {getIcon(item.category)}
                    </div>
                    
                    <button 
                            onClick={(e) => handleDelete(item.id, e)}
                            className="opacity-0 group-hover:opacity-100 p-1.5 text-gray-500 hover:text-red-400 hover:bg-red-500/10 rounded-md transition-all"
                            title="Remove Link"
                    >
                            <Trash2 size={12} />
                    </button>
                    </div>
                    <span className="text-xs font-medium text-gray-300 group-hover:text-white truncate" title={item.title}>
                    {item.title}
                    </span>
                </a>
                ))}
                
                {/* Add New Button */}
                <button 
                    onClick={() => setIsAdding(true)}
                    className="flex flex-col items-center justify-center p-3 rounded-xl border border-dashed border-white/10 hover:border-nexus-purple/50 text-gray-500 hover:text-nexus-purple transition-all gap-2 min-h-[88px] group"
                >
                    <div className="w-8 h-8 rounded-full bg-white/5 group-hover:bg-nexus-purple/20 flex items-center justify-center transition-colors">
                        <Plus size={16} />
                    </div>
                    <span className="text-[10px] font-medium">Add Resource</span>
                </button>
            </div>
            </div>
        </>
      )}
    </div>
  );
};

export default Vault;
