import React, { useState } from 'react';
import { ChevronLeft, ChevronRight, Plus, Calendar as CalendarIcon, Clock, Trash2, X } from 'lucide-react';

const Agenda = ({ searchQuery = '' }) => {
  // State for Calendar Navigation
  const [currentDate, setCurrentDate] = useState(new Date()); // Tracks the Month/Year being viewed
  const [selectedDate, setSelectedDate] = useState(new Date()); // Tracks the specific day selected
  
  // State for Events
  // Initial dummy data spread across different dates for demonstration
  const [events, setEvents] = useState([
    { id: '1', title: 'Web Dev Lecture', startTime: new Date(), endTime: new Date(new Date().setHours(new Date().getHours() + 2)), type: 'lecture' },
    { id: '2', title: 'Team Meeting', startTime: new Date(new Date().setDate(new Date().getDate() + 1)), endTime: new Date(new Date().setDate(new Date().getDate() + 1)), type: 'meeting' },
    { id: '3', title: 'Project Deadline', startTime: new Date(new Date().getFullYear(), 11, 25), endTime: new Date(new Date().getFullYear(), 11, 25), type: 'personal' }, // Example for Dec 25
  ]);

  // State for Adding New Event
  const [isAdding, setIsAdding] = useState(false);
  const [newEventTitle, setNewEventTitle] = useState('');
  const [newEventType, setNewEventType] = useState('personal');

  // --- Calendar Logic Helpers ---

  const getDaysInMonth = (date) => {
    return new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
  };

  const getFirstDayOfMonth = (date) => {
    return new Date(date.getFullYear(), date.getMonth(), 1).getDay();
  };

  const changeMonth = (increment) => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + increment, 1));
  };

  const changeYear = (increment) => {
    setCurrentDate(new Date(currentDate.getFullYear() + increment, currentDate.getMonth(), 1));
  };

  const isSameDay = (d1, d2) => {
    return d1.getFullYear() === d2.getFullYear() &&
           d1.getMonth() === d2.getMonth() &&
           d1.getDate() === d2.getDate();
  };

  const handleDateClick = (day) => {
    const newSelectedDate = new Date(currentDate.getFullYear(), currentDate.getMonth(), day);
    setSelectedDate(newSelectedDate);
    setIsAdding(false); // Close add form when switching dates
  };

  // --- Event Logic ---

  const handleAddEvent = () => {
    if (!newEventTitle.trim()) return;

    const start = new Date(selectedDate);
    start.setHours(9, 0, 0); // Default start time 09:00
    const end = new Date(selectedDate);
    end.setHours(10, 0, 0); // Default end time 10:00

    const newEvent = {
      id: Date.now().toString(),
      title: newEventTitle,
      startTime: start,
      endTime: end,
      type: newEventType
    };

    setEvents([...events, newEvent]);
    setNewEventTitle('');
    setIsAdding(false);
  };

  const deleteEvent = (id, e) => {
    e.stopPropagation();
    setEvents(events.filter(ev => ev.id !== id));
  };

  // Filter events for the *selected date*
  const selectedDateEvents = events.filter(event => 
    isSameDay(new Date(event.startTime), selectedDate) &&
    (!searchQuery || event.title.toLowerCase().includes(searchQuery.toLowerCase()))
  );

  // Constants
  const daysOfWeek = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];
  const monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

  // Grid Generation
  const daysInMonth = getDaysInMonth(currentDate);
  const firstDay = getFirstDayOfMonth(currentDate); // 0 = Sunday
  
  // Create array for empty slots before the 1st of the month
  const blanks = Array(firstDay).fill(null);
  // Create array for actual days
  const days = Array.from({ length: daysInMonth }, (_, i) => i + 1);
  // Total slots
  const totalSlots = [...blanks, ...days];


  return (
    <div className="flex flex-col h-full overflow-hidden">
      
      {/* --- Calendar Navigation Header --- */}
      <div className="flex flex-col gap-2 mb-4 shrink-0">
        
        {/* Month & Year Controls */}
        <div className="flex items-center justify-between bg-white/5 rounded-xl p-2 border border-white/5">
            <div className="flex items-center gap-1">
                <button onClick={() => changeMonth(-1)} className="p-1 hover:bg-white/10 rounded-lg text-gray-400 hover:text-white transition-colors">
                    <ChevronLeft size={16} />
                </button>
                <span className="text-sm font-bold w-24 text-center text-white select-none">
                    {monthNames[currentDate.getMonth()]}
                </span>
                <button onClick={() => changeMonth(1)} className="p-1 hover:bg-white/10 rounded-lg text-gray-400 hover:text-white transition-colors">
                    <ChevronRight size={16} />
                </button>
            </div>

            <div className="h-4 w-px bg-white/10" />

            <div className="flex items-center gap-1">
                <button onClick={() => changeYear(-1)} className="p-1 hover:bg-white/10 rounded-lg text-gray-400 hover:text-white transition-colors">
                    <ChevronLeft size={16} />
                </button>
                <span className="text-sm font-mono font-bold text-nexus-teal w-12 text-center select-none">
                    {currentDate.getFullYear()}
                </span>
                <button onClick={() => changeYear(1)} className="p-1 hover:bg-white/10 rounded-lg text-gray-400 hover:text-white transition-colors">
                    <ChevronRight size={16} />
                </button>
            </div>
        </div>

        {/* Days of Week Header */}
        <div className="grid grid-cols-7 gap-1 text-center mt-2">
            {daysOfWeek.map(d => (
            <div key={d} className="text-[10px] text-gray-500 font-bold uppercase tracking-wider">{d}</div>
            ))}
        </div>
      </div>

      {/* --- Calendar Grid --- */}
      <div className="grid grid-cols-7 gap-1 mb-6 shrink-0">
        {totalSlots.map((day, index) => {
            if (!day) return <div key={`blank-${index}`} className="h-8" />;

            const currentDayDate = new Date(currentDate.getFullYear(), currentDate.getMonth(), day);
            const isToday = isSameDay(currentDayDate, new Date());
            const isSelected = isSameDay(currentDayDate, selectedDate);
            // Check if this specific day has events
            const hasEvents = events.some(e => isSameDay(new Date(e.startTime), currentDayDate));

            return (
                <div 
                    key={day} 
                    onClick={() => handleDateClick(day)}
                    className={`
                        h-8 flex flex-col items-center justify-center rounded-lg cursor-pointer transition-all relative group
                        ${isSelected 
                            ? 'bg-nexus-purple text-white shadow-[0_0_10px_rgba(115,42,223,0.4)] scale-105 z-10 font-bold' 
                            : isToday 
                                ? 'bg-nexus-teal/20 text-nexus-teal border border-nexus-teal/50' 
                                : 'text-gray-400 hover:bg-white/10 hover:text-white'}
                    `}
                >
                    <span className="text-xs">{day}</span>
                    {hasEvents && !isSelected && (
                        <div className="absolute bottom-1 w-1 h-1 rounded-full bg-nexus-teal" />
                    )}
                </div>
            );
        })}
      </div>

      <div className="h-px w-full bg-white/10 mb-4 shrink-0" />

      {/* --- Events List Section --- */}
      <div className="flex items-center justify-between mb-3 shrink-0">
          <h4 className="text-xs uppercase tracking-widest text-gray-500 font-bold flex items-center gap-2">
            <CalendarIcon size={12} />
            {selectedDate.toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' })}
          </h4>
          
          {!isAdding && (
             <button 
                onClick={() => setIsAdding(true)} 
                className="text-[10px] bg-nexus-teal/10 text-nexus-teal hover:bg-nexus-teal hover:text-black px-2 py-1 rounded-md transition-all font-bold flex items-center gap-1"
            >
                 <Plus size={10} /> Add
             </button>
          )}
      </div>

      <div className="flex-1 overflow-y-auto pr-1 custom-scrollbar">
        {/* Add Event Form */}
        {isAdding && (
            <div className="bg-white/5 rounded-xl p-3 mb-3 border border-nexus-teal/30 animate-fade-in">
                <div className="flex justify-between items-start mb-2">
                    <span className="text-[10px] text-nexus-teal font-bold uppercase">New Entry</span>
                    <button onClick={() => setIsAdding(false)} className="text-gray-500 hover:text-white"><X size={12}/></button>
                </div>
                <input 
                    type="text"
                    value={newEventTitle}
                    onChange={(e) => setNewEventTitle(e.target.value)}
                    placeholder="Event title..."
                    className="w-full bg-black/20 border border-white/10 rounded-lg px-2 py-1.5 text-sm text-white focus:border-nexus-teal outline-none mb-2"
                    autoFocus
                    onKeyDown={(e) => e.key === 'Enter' && handleAddEvent()}
                />
                <div className="flex gap-2 mb-2">
                    {['lecture', 'meeting', 'personal'].map(t => (
                        <button 
                            key={t}
                            onClick={() => setNewEventType(t)}
                            className={`px-2 py-1 rounded text-[9px] uppercase font-bold border ${newEventType === t ? 'bg-nexus-purple text-white border-nexus-purple' : 'bg-transparent text-gray-500 border-white/10'}`}
                        >
                            {t}
                        </button>
                    ))}
                </div>
                <button 
                    onClick={handleAddEvent}
                    className="w-full bg-nexus-teal text-nexus-deep text-xs font-bold py-1.5 rounded-lg hover:bg-white transition-colors"
                >
                    Confirm Schedule
                </button>
            </div>
        )}

        {/* List of Events */}
        {selectedDateEvents.length > 0 ? (
          <div className="space-y-2">
              {selectedDateEvents.map(event => (
                <div key={event.id} className="relative pl-4 border-l-2 border-nexus-teal/30 hover:border-nexus-teal transition-all group bg-white/5 hover:bg-white/10 rounded-r-lg p-2">
                  <div className="absolute -left-[5px] top-2 w-2 h-2 rounded-full bg-nexus-teal shadow-[0_0_8px_#65BBBD] group-hover:scale-125 transition-transform" />
                  
                  <div className="flex justify-between items-start">
                      <div>
                        <h5 className="text-sm font-semibold text-gray-200 group-hover:text-white transition-colors">{event.title}</h5>
                        <div className="flex items-center gap-3 mt-1">
                            <span className="text-[10px] text-nexus-purple font-mono flex items-center gap-1">
                                <Clock size={10} />
                                {event.startTime.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'})}
                            </span>
                            <span className="text-[9px] uppercase font-bold tracking-wider text-gray-500 border border-white/10 px-1.5 rounded">{event.type}</span>
                        </div>
                      </div>
                      
                      <button 
                        onClick={(e) => deleteEvent(event.id, e)}
                        className="opacity-0 group-hover:opacity-100 p-1.5 text-gray-500 hover:text-red-400 hover:bg-red-500/10 rounded transition-all"
                      >
                          <Trash2 size={12} />
                      </button>
                  </div>
                </div>
              ))}
          </div>
        ) : (
           !isAdding && (
               <div className="h-32 flex flex-col items-center justify-center text-gray-600 border border-dashed border-white/10 rounded-xl">
                   <CalendarIcon size={24} className="mb-2 opacity-50" />
                   <span className="text-xs italic">No timeline entries.</span>
                   <button onClick={() => setIsAdding(true)} className="mt-2 text-[10px] text-nexus-teal hover:underline">Create Event</button>
               </div>
           )
        )}
      </div>
    </div>
  );
};

export default Agenda;
